// LoadingContainer는 현재 사용되지 않음 (LoadingSpinner 컴포넌트 사용)

const ErrorContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #f44336;
  text-align: center;
  padding: 20px;
`;

const ReaderView = () => {
  const { bookId } = useParams();
  const navigate = useNavigate();
  const { getBookById, getBookWithFile, saveBookmark, getBookmark, saveReadingSettings, getReadingSettings } = useBooks();
  
  const [book, setBook] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // UI 상태
  const [showTOC, setShowTOC] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [showChat, setShowChat] = useState(false);
  const [chatSidebarWidth, setChatSidebarWidth] = useState(600);
  const [isResizing, setIsResizing] = useState(false);
  
  // ReactReaderRenderer ref
  const reactReaderRef = useRef(null);
  
  // 읽기 설정
  const [readingSettings, setReadingSettings] = useState({
    fontSize: 16,
    fontFamily: 'serif',
    lineHeight: 1.6,
    theme: 'light',
    margin: 20,
    textAlign: 'left'
  });
  
  // 북마크 관련 상태
  const [lastLocation, setLastLocation] = useState(null); // EPUB 위치 추적용
  const [pendingBookmarkLocation, setPendingBookmarkLocation] = useState(null); // 북마크 위치 복원 대기
  
  // 새로고침 감지 및 캐시 복구
  const [isRefreshRecovery, setIsRefreshRecovery] = useState(false);




  


  // 페이지 새로고침 감지
  useEffect(() => {
    const handleBeforeUnload = () => {
      // 새로고침 플래그 설정
      sessionStorage.setItem('epub-reader-refresh', 'true');
    };

    const handleLoad = () => {
      // 새로고침 후 복구 플래그 확인
      const wasRefresh = sessionStorage.getItem('epub-reader-refresh');
      if (wasRefresh) {
        setIsRefreshRecovery(true);
        sessionStorage.removeItem('epub-reader-refresh');
        console.log('🔄 페이지 새로고침 감지, 캐시 복구 모드 활성화');
      }
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    window.addEventListener('load', handleLoad);
    
    // 컴포넌트 마운트 시에도 확인
    handleLoad();

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
      window.removeEventListener('load', handleLoad);
    };
  }, []);

  useEffect(() => {
    let isMounted = true; // cleanup을 위한 플래그

    const loadBook = async () => {
      try {
        setLoading(true);
        
        // 1단계: 메타데이터 로드 (새로고침 대응)
        let basicBook = getBookById(bookId);
        
        // 새로고침 시 BookContext가 아직 로드되지 않은 경우 직접 IndexedDB에서 로드
        if (!basicBook) {
          console.log('📚 BookContext에서 책을 찾을 수 없음, IndexedDB에서 직접 로드 시도...');
          
          try {
            // IndexedDB에서 직접 책 메타데이터 로드
            const indexedDBStorage = (await import('../../utils/IndexedDBStorage')).default;
            await indexedDBStorage.init(3000); // 3초 타임아웃
            
            const bookFromDB = await indexedDBStorage.getBookMetadata(bookId);
            if (bookFromDB) {
              console.log('✅ IndexedDB에서 책 메타데이터 로드 성공:', bookFromDB.title);
              basicBook = bookFromDB;
            } else {
              console.error('❌ IndexedDB에서도 책을 찾을 수 없음:', bookId);
              if (isMounted) {
                setError('책을 찾을 수 없습니다. 라이브러리로 돌아가서 다시 선택해주세요.');
              }
              return;
            }
          } catch (dbError) {
            console.error('❌ IndexedDB 직접 로드 실패:', dbError);
            if (isMounted) {
              setError('책 데이터를 로드할 수 없습니다. 페이지를 새로고침해보세요.');
            }
            return;
          }
        }
        
        
        // 2단계: IndexedDB에서 실제 파일 데이터 로드 (새로고침 대응)
        let bookWithFile = { ...basicBook };
        
        try {
          // BookContext의 getBookWithFile 함수 먼저 시도
          let bookWithFileData = null;
          
          if (getBookWithFile) {
            try {
              bookWithFileData = await getBookWithFile(bookId);
            } catch (contextError) {
              console.warn('⚠️ BookContext에서 파일 로드 실패:', contextError);
            }
          }
          
          // BookContext에서 실패하거나 데이터가 없으면 직접 IndexedDB에서 로드
          if (!bookWithFileData || (!bookWithFileData.fileData && !bookWithFileData.content)) {
            console.log('📁 직접 IndexedDB에서 파일 데이터 로드 시도...');
            
            const indexedDBStorage = (await import('../../utils/IndexedDBStorage')).default;
            const fileData = await indexedDBStorage.getBookFile(bookId);
            
            if (fileData) {
              console.log('✅ IndexedDB에서 파일 데이터 로드 성공');
              bookWithFileData = { fileData };
            }
          }
          
          if (bookWithFileData && (bookWithFileData.fileData || bookWithFileData.content)) {
            console.log('📖 파일 데이터 설정 완료');
            bookWithFile = {
              ...basicBook,
              fileData: bookWithFileData.fileData || bookWithFileData.content
            };
          } else {
            console.warn('⚠️ 파일 데이터를 찾을 수 없음');
            bookWithFile.content = `
              <div style="padding: 40px; text-align: center;">
                <h1>${basicBook.title}</h1>
                <p>작가: ${basicBook.author}</p>
                <p style="color: #f44336; margin: 20px 0;">파일 데이터를 로드할 수 없습니다.</p>
                <p>책이 제대로 업로드되지 않았을 수 있습니다.</p>
                <p>라이브러리로 돌아가서 책을 다시 업로드해주세요.</p>
                <p style="margin-top: 30px; color: #666; font-size: 0.9em;">
                  파일 크기: ${(basicBook.size / 1024 / 1024).toFixed(2)} MB<br/>
                  파일 형식: ${basicBook.type?.toUpperCase() || '알 수 없음'}
                </p>
              </div>
            `;
          }
        } catch (dbError) {
          console.error('❌ 파일 데이터 로드 실패:', dbError);
          bookWithFile.content = `
            <div style="padding: 40px; text-align: center;">
              <h1>${basicBook.title}</h1>
              <p style="color: #f44336;">데이터베이스 오류가 발생했습니다.</p>
              <p>오류: ${dbError.message}</p>
              <p>페이지를 새로고침하거나 브라우저 캐시를 정리해보세요.</p>
            </div>
          `;
        }
        
        
        if (isMounted) {
          setBook(bookWithFile);
          setLoading(false);
        }
        
        // 저장된 읽기 설정 로드
        const savedSettings = await getReadingSettings(bookId);
        if (savedSettings && isMounted) {
          setReadingSettings(savedSettings);
        }
        
        // 북마크 로드 (저장된 읽기 위치)
        const savedBookmark = await getBookmark(bookId);
        if (savedBookmark && isMounted) {
          console.log('🔖 저장된 북마크 발견:', savedBookmark);
          
          // EPUB의 경우 location 정보 복원 (렌더러가 준비된 후)
          if (savedBookmark.location && bookWithFile.type === 'epub') {
            // 북마크 위치 정보를 저장해두고, 렌더러가 준비된 후 이동
            setLastLocation(savedBookmark.location);
            console.log('🔖 EPUB 위치 정보 저장:', savedBookmark.location);
            
            // 북마크 위치 복원 플래그 설정 (ReactReaderRenderer에서 처리)
            setPendingBookmarkLocation(savedBookmark.location);
          }
        }
        
      } catch (err) {
        if (isMounted) {
          setError('책을 로드하는 중 오류가 발생했습니다.');
          console.error('Book loading error:', err);
        }
      } finally {
        if (isMounted) {
          setLoading(false);
        }
      }
    };

    if (bookId) {
      loadBook();
    }

    return () => {
      isMounted = false;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bookId]); // getBookById, getBookWithFile, getReadingSettings는 안정적이므로 의존성에서 제외

  // 읽기 설정 변경 시 저장
  useEffect(() => {
    if (book && bookId) {
      saveReadingSettings(bookId, readingSettings);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [readingSettings, bookId, book]); // saveReadingSettings는 안정적이므로 의존성에서 제외

  // 위치 변경 시 북마크 저장
  useEffect(() => {
    // 북마크 저장 조건 확인
    if (book && 
        book.id && 
        bookId &&
        book.id === bookId) { // bookId와 book.id 일치 확인
      
      // 북마크 정보 생성 (안전한 데이터만 포함)
      const bookmarkData = {
        location: typeof lastLocation === 'string' ? lastLocation : null, // 문자열만 허용
        timestamp: new Date().toISOString()
      };

      // 디바운스를 사용하여 너무 자주 저장되지 않도록 함
      const timeoutId = setTimeout(async () => {
        try {
          await saveBookmark(bookId, bookmarkData);
          console.log(`🔖 북마크 자동 저장: 위치 ${bookmarkData.location}`);
        } catch (error) {
          console.warn('⚠️ 북마크 저장 실패:', error);
        }
      }, 2000); // 2초 디바운스로 늘려서 더 안정적으로

      return () => clearTimeout(timeoutId);
    } else {
      console.log('🔖 북마크 저장 조건 미충족:', {
        book: !!book,
        bookId: bookId,
        bookIdMatch: book?.id === bookId,
        lastLocation: lastLocation
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bookId, book, lastLocation]); // saveBookmark는 안정적이므로 의존성에서 제외

  const handleBack = () => {
    navigate('/');
  };

  const handlePageChange = useCallback((page) => {
    console.log('📄 ReaderView handlePageChange 호출:', typeof page, page);
    
    // EPUB 위치 변경 처리 (북마크 저장용)
    if (typeof page === 'object' && page !== null) {
      if (page.type === 'location_changed' || page.type === 'relocated') {
        // EPUB 위치 정보 저장 (북마크용)
        if (page.location) {
          setLastLocation(page.location);
          console.log('🔖 북마크 위치 저장:', page.location);
          
          // 북마크 저장
          if (book?.id) {
            saveBookmark(book.id, page.location);
          }
        }
        return;
      }
      
      // 챕터 네비게이션 처리
      if (page.type === 'chapter') {
        console.log('📖 챕터 네비게이션:', page);
        
        if (reactReaderRef.current) {
          const chapterHref = page.href;
          if (chapterHref) {
            reactReaderRef.current.goToChapter(chapterHref);
          } else {
            console.warn('⚠️ 챕터 href를 찾을 수 없음');
          }
        }
        return;
      }
      
      console.warn('⚠️ 알 수 없는 객체 타입:', page);
      return;
    } 
    
    console.warn('⚠️ 지원되지 않는 페이지 형식:', typeof page, page);
  }, [book?.id, saveBookmark]);

  const handleSettingsChange = useCallback((newSettings) => {
    console.log('🔧 ReaderView 설정 변경:', newSettings);
    setReadingSettings(prev => {
      const updated = { ...prev, ...newSettings };
      console.log('📋 최종 설정:', updated);
      return updated;
    });
  }, []);



  const handleTextSelection = (selectedText, position) => {
    // startChat 플래그가 있을 때만 AI 채팅 열기
    if (position && position.startChat) {
      setShowChat(true);
    }
  };

  // 사이드바 리사이징 핸들러
  const handleResizeStart = (e) => {
    setIsResizing(true);
    const startX = e.clientX;
    const startWidth = chatSidebarWidth;

    const handleMouseMove = (e) => {
      const deltaX = startX - e.clientX;
      const newWidth = Math.max(400, Math.min(800, startWidth + deltaX));
      setChatSidebarWidth(newWidth);
    };

    const handleMouseUp = () => {
      setIsResizing(false);
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };

    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  // 렌더러 에러 핸들러
  const handleRendererError = (error) => {
    console.error('🚨 렌더러 에러:', error);
    
    // 새로고침 복구 모드에서 ServiceWorker 캐시 관련 에러인 경우 자동 재시도
    if (isRefreshRecovery && (
      error.message.includes('EPUB data not found') ||
      error.message.includes('파일 데이터를 찾을 수 없습니다') ||
      error.message.includes('ServiceWorker') ||
      error.message.includes('프록시 URL')
    )) {
      console.log('🔄 새로고침 복구 모드: 강화된 자동 재시도 시작...');
      
      setError('새로고침으로 인한 데이터 손실을 복구하고 있습니다...');
      
      // 단계별 복구 시도
      let retryAttempt = 0;
      const maxRetries = 3;
      
      const attemptRecovery = async () => {
        retryAttempt++;
        console.log(`🔄 복구 시도 ${retryAttempt}/${maxRetries}`);
        
        try {
          if (retryAttempt === 1) {
            // 1차: ServiceWorker 재등록
            console.log('🔄 1차 복구: ServiceWorker 재등록');
            if ('serviceWorker' in navigator) {
              await navigator.serviceWorker.getRegistrations().then(registrations => {
                registrations.forEach(registration => registration.unregister());
              });
              await navigator.serviceWorker.register('/epub-proxy-worker.js');
              await navigator.serviceWorker.ready;
            }
            
            // 3초 후 페이지 새로고침
            setTimeout(() => {
              if (retryAttempt === 1) {
                window.location.reload();
              }
            }, 3000);
            
          } else if (retryAttempt === 2) {
            // 2차: 캐시 정리 후 재시도
            console.log('🔄 2차 복구: 캐시 정리 후 재시도');
            if ('caches' in window) {
              const cacheNames = await caches.keys();
              await Promise.all(
                cacheNames.map(cacheName => caches.delete(cacheName))
              );
            }
            
            // 5초 후 페이지 새로고침
            setTimeout(() => {
              if (retryAttempt === 2) {
                window.location.reload();
              }
            }, 5000);
            
          } else {
            // 3차: 완전 초기화
            console.log('🔄 3차 복구: 완전 초기화');
            
            // localStorage 정리
            Object.keys(localStorage).forEach(key => {
              if (key.includes('reader') || key.includes('epub')) {
                localStorage.removeItem(key);
              }
            });
            
            // sessionStorage 정리
            sessionStorage.clear();
            
            // 최종 재시도
            setTimeout(() => {
              window.location.reload();
            }, 2000);
          }
          
        } catch (recoveryError) {
          console.error(`❌ ${retryAttempt}차 복구 실패:`, recoveryError);
          
          if (retryAttempt < maxRetries) {
            setTimeout(attemptRecovery, 3000);
          } else {
            setError('자동 복구에 실패했습니다. 라이브러리로 돌아가서 책을 다시 선택해주세요.');
          }
        }
      };
      
      // 첫 번째 복구 시도 시작
      setTimeout(attemptRecovery, 1000);
      return;
    }
    
    // ReactReader EPUB 구조 분석 실패 시 TextRenderer로 자동 전환
    if (error.message && (
      error.message.includes('ReactReader EPUB 구조 분석 실패') ||
      error.message.includes('TextRenderer로 자동 전환') ||
      error.message.includes('package') ||
      error.message.includes('Cannot read properties of undefined')
    )) {
      console.log('🔄 ReactReader 실패 - TextRenderer로 자동 전환');
      
      // book 객체를 TextRenderer용으로 수정
      if (book) {
        const modifiedBook = {
          ...book,
          type: 'text', // 강제로 text 타입으로 변경
          useTextRenderer: true // 플래그 추가
        };
        
        setBook(modifiedBook);
        setError(null); // 에러 상태 초기화
        
        console.log('✅ TextRenderer로 전환 완료');
        return;
      }
    }
    
    setError('렌더러 오류가 발생했습니다: ' + error.message);
  };

  // 렌더러 선택 로직은 주석으로만 유지
  // book?.type, rendererType, book.hasFile 확인
  
  if (loading) {
    return (
      <Container>
        <LoadingSpinner 
          message="책을 로드하는 중..." 
          subtext="IndexedDB에서 파일 데이터를 불러오고 있습니다"
        />
      </Container>
    );
  }

  if (error || (!loading && !book)) {
    return (
      <Container>
        <ErrorContainer>
          <h3>📚 책 로드 중 문제 발생</h3>
          <p>{error || '책을 찾을 수 없습니다.'}</p>
          
          {/* 새로고침 관련 에러인 경우 복구 옵션 제공 */}
          {error && (
            error.includes('EPUB data not found') ||
            error.includes('파일 데이터를 찾을 수 없습니다') ||
            error.includes('ServiceWorker') ||
            error.includes('프록시 URL') ||
            error.includes('새로고침으로 인한') ||
            error.includes('복구하고 있습니다')
          ) && (
            <div style={{ marginTop: '20px', textAlign: 'center' }}>
              <div style={{ 
                background: '#e3f2fd', 
                border: '1px solid #bbdefb', 
                borderRadius: '8px', 
                padding: '20px', 
                marginBottom: '20px' 
              }}>
                <h4 style={{ color: '#1565c0', margin: '0 0 10px 0' }}>
                  🔄 자동 복구 진행 중
                </h4>
                <p style={{ color: '#1976d2', margin: '0', fontSize: '0.9rem', lineHeight: '1.4' }}>
                  페이지 새로고침으로 인해 일시적으로 데이터가 손실되었습니다.<br/>
                  시스템이 자동으로 복구를 시도하고 있습니다.
                </p>
                
                {error.includes('복구하고 있습니다') && (
                  <div style={{ marginTop: '15px' }}>
                    <div style={{ 
                      width: '100%', 
                      height: '4px', 
                      background: '#e0e0e0', 
                      borderRadius: '2px',
                      overflow: 'hidden'
                    }}>
                      <div style={{
                        width: '100%',
                        height: '100%',
                        background: 'linear-gradient(90deg, #4CAF50, #45a049)',
                        animation: 'progress 2s ease-in-out infinite'
                      }} />
                    </div>
                    <style>{`
                      @keyframes progress {
                        0% { transform: translateX(-100%); }
                        100% { transform: translateX(100%); }
                      }
                    `}</style>
                    <p style={{ 
                      fontSize: '0.8rem', 
                      color: '#666', 
                      margin: '10px 0 0 0' 
                    }}>
                      잠시만 기다려주세요... 자동으로 복구됩니다.
                    </p>
                  </div>
                )}
              </div>
              
              <div style={{ display: 'flex', gap: '10px', justifyContent: 'center', flexWrap: 'wrap' }}>
                <button 
                  onClick={() => window.location.reload()}
                  style={{
                    padding: '12px 24px',
                    backgroundColor: '#4CAF50',
                    color: 'white',
                    border: 'none',
                    borderRadius: '8px',
                    cursor: 'pointer',
                    fontSize: '0.9rem',
                    fontWeight: '500'
                  }}
                >
                  🔄 수동으로 다시 시도
                </button>
                
                <button 
                  onClick={handleBack}
                  style={{
                    padding: '12px 24px',
                    backgroundColor: '#f8f9fa',
                    color: '#666',
                    border: '1px solid #e0e0e0',
                    borderRadius: '8px',
                    cursor: 'pointer',
                    fontSize: '0.9rem',
                    fontWeight: '500'
                  }}
                >
                  📚 라이브러리로 돌아가기
                </button>
              </div>
              
              <div style={{ 
                marginTop: '20px', 
                padding: '15px', 
                background: '#fff3e0', 
                border: '1px solid #ffcc02',
                borderRadius: '8px',
                fontSize: '0.85rem',
                color: '#e65100'
              }}>
                <strong>💡 도움말:</strong><br/>
                • 새로고침 후 일시적으로 발생할 수 있는 문제입니다<br/>
                • 대부분 자동으로 해결됩니다 (10-30초 소요)<br/>
                • 계속 문제가 발생하면 브라우저 캐시를 정리해보세요
              </div>
            </div>
          )}
          
          {/* 일반 에러인 경우 기본 버튼만 */}
          {(!error || !(
            error.includes('EPUB data not found') ||
            error.includes('파일 데이터를 찾을 수 없습니다') ||
            error.includes('ServiceWorker') ||
            error.includes('프록시 URL') ||
            error.includes('새로고침으로 인한') ||
            error.includes('복구하고 있습니다')
          )) && (
            <div style={{ marginTop: '20px' }}>
              <button 
                onClick={handleBack}
                style={{
                  padding: '12px 24px',
                  backgroundColor: '#f8f9fa',
                  color: '#666',
                  border: '1px solid #e0e0e0',
                  borderRadius: '8px',
                  cursor: 'pointer',
                  fontSize: '0.9rem',
                  fontWeight: '500'
                }}
              >
                📚 라이브러리로 돌아가기
              </button>
            </div>
          )}
        </ErrorContainer>
      </Container>
    );
  }

  return (
    <Container>
      <Sidebar isOpen={showTOC}>
        {book && (
          <TableOfContents
            book={book}
            onPageChange={handlePageChange}
            onClose={() => setShowTOC(false)}
          />
        )}
      </Sidebar>

      <MainContent>
        <Header>
          <HeaderLeft>
            <BackButton onClick={handleBack} title="라이브러리로 돌아가기">
              <FiArrowLeft size={20} />
            </BackButton>
            <BookTitle>{book?.title || '제목 없음'}</BookTitle>
          </HeaderLeft>

          <HeaderRight>
            <HeaderButton
              active={showTOC}
              onClick={() => setShowTOC(!showTOC)}
              title="목차"
            >
              <FiBookmark size={18} />
            </HeaderButton>
            
            <HeaderButton
              active={showSettings}
              onClick={() => setShowSettings(!showSettings)}
              title="읽기 설정"
            >
              <FiSettings size={18} />
            </HeaderButton>
            
            <HeaderButton
              active={showChat}
              onClick={() => setShowChat(!showChat)}
              title="AI 채팅"
            >
              <FiMessageSquare size={18} />
            </HeaderButton>
            

          </HeaderRight>
        </Header>

        <ReadingArea>
          <ContentArea>
            {(book?.type === 'epub' && !book?.useTextRenderer) ? (
              <ReactReaderRenderer
                ref={reactReaderRef}
                key={`react-reader-${book?.id}`}
                book={book}
                settings={readingSettings}
                onPageChange={handlePageChange}
                onTextSelection={handleTextSelection}
                onError={handleRendererError}
                pendingBookmarkLocation={pendingBookmarkLocation}
                onBookmarkRestored={() => setPendingBookmarkLocation(null)}
              />
            ) : (
              <TextRenderer
                book={book}
                settings={readingSettings}
                onPageChange={handlePageChange}
                onTextSelection={handleTextSelection}
                onToggleTOC={() => setShowTOC(!showTOC)}
              />
            )}
            
            {showSettings && (
              <ReadingSettings
                settings={readingSettings}
                onSettingsChange={handleSettingsChange}
                onClose={() => setShowSettings(false)}
              />
            )}
          </ContentArea>

          <ChatSidebar 
            isOpen={showChat} 
            width={chatSidebarWidth}
            isResizing={isResizing}
          >
            {showChat && (
              <ResizeHandle 
                onMouseDown={handleResizeStart}
                title="사이드바 크기 조절"
              />
            )}
            <AIChat
              book={book}
              onClose={() => setShowChat(false)}
            />
          </ChatSidebar>
        </ReadingArea>
      </MainContent>
    </Container>
  );
};

export default ReaderView; 